@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/Ubuntu-Light.woff2") format("woff2"), url("../fonts/Ubuntu-Light.woff") format("woff");
}
@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2"), url("../fonts/Ubuntu-Regular.woff") format("woff");
}
@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Ubuntu-Medium.woff2") format("woff2"), url("../fonts/Ubuntu-Medium.woff") format("woff");
}
@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Ubuntu-Bold.woff2") format("woff2"), url("../fonts/Ubuntu-Bold.woff") format("woff");
}
.page-container {
  width: 100%;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .page-container {
    width: 100%;
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media (min-width: 1366px) {
  .page-container {
    width: 100%;
    padding-left: 64px;
    padding-right: 64px;
  }
}
.modal-container {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(28, 28, 28, 0.9);
  z-index: 10;
}
.modal-container.modal-opened {
  display: block;
}
.modal-container--nojs {
  position: static;
  display: block;
  height: 100vh;
  background-color: rgba(28, 28, 28, 0.5);
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-section {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding-top: 62px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .page-section {
    padding-top: 115px;
    padding-left: 26px;
    padding-right: 0;
    padding-bottom: 30px;
    box-sizing: border-box;
  }
}
@media (min-width: 1366px) {
  .page-section {
    padding-top: 76px;
  }
}
.button-base {
  max-width: 158px;
  padding: 8px 13px;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 2px solid #0dd1a2;
  border-radius: 2px;
  box-sizing: border-box;
  cursor: pointer;
}
.button-base:hover {
  background-color: #0aa081;
  border-color: #0aa081;
}
.button-base:active {
  background-color: #0dd1a2;
  border-color: #0dd1a2;
}
.button-base--big {
  display: block;
  min-width: 207px;
  padding: 14px 30px;
  padding-left: 69px;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  border: 3px solid #15b38d;
  border-radius: 3px;
}
@media (min-width: 1366px) {
  .button-base--big {
    min-width: 238px;
    padding: 20px 40px;
    padding-left: 80px;
    font-size: 21px;
    line-height: 24px;
  }
}
.button-base--big-fill {
  background-color: #15b38d;
}
@media (min-width: 768px) {
  .contacts {
    padding-left: 0;
    padding-right: 0;
  }
}
.contacts__content-container {
  padding-top: 60px;
  flex: 1 0 auto;
}
@media (min-width: 1366px) {
  .contacts__content-container {
    padding-top: 75px;
    padding-left: 31px;
    padding-right: 26px;
  }
}
.contacts__title {
  margin: 0;
  margin-bottom: 31px;
}
.contacts__list {
  margin: 0;
  margin-bottom: 54px;
}
.contacts__item {
  margin-bottom: 33px;
}
.contacts__city {
  padding-bottom: 13px;
}
.contacts__item dd {
  margin: 0;
  margin-bottom: 5px;
}
@media (min-width: 768px) and (max-width: 1365px) {
  .contacts__list {
    display: flex;
    justify-content: space-between;
    max-width: 850px;
    margin-bottom: 10px;
  }
  .contacts__feedback {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 1366px) {
  .contacts__content-container {
    display: grid;
    grid-template-columns: 30px 240px 60px 580px;
    grid-template-rows: min-content 50px max-content;
  }
  .contacts__title {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    margin-bottom: 0;
    margin-right: auto;
  }
  .contacts__list {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
    margin: 0;
    padding: 0;
  }
  .contacts__feedback {
    grid-column: 4 / 5;
    grid-row: 3 / 4;
  }
}
.feedback-form {
  padding-right: 8px;
}
.feedback__title {
  margin: 0;
  margin-bottom: 10px;
}
@media (min-width: 1366px) {
  .feedback__title {
    margin-bottom: 14px;
  }
}
.feedback__text {
  margin: 0;
  margin-bottom: 10px;
  padding-right: 40px;
}
@media (min-width: 1366px) {
  .feedback__text {
    margin-bottom: 14px;
  }
}
.feedback-form__fieldset {
  padding: 0;
  border: none;
}
.feedback-form__field {
  position: relative;
}
.feedback-form__input {
  width: 100%;
  margin-bottom: 10px;
  padding: 15px;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-size: 13px;
  line-height: 17px;
  color: #1c1c1c;
  background-color: #ffffff;
  opacity: 0.95;
  border: none;
  box-sizing: border-box;
}
@media (min-width: 1366px) {
  .feedback-form__input {
    padding-top: 14px;
    padding-bottom: 16px;
  }
}
.feedback-form__input--textarea {
  min-height: 165px;
  margin-bottom: 0;
}
@media (min-width: 1366px) {
  .feedback-form__input--textarea {
    min-height: 144px;
    padding-top: 13px;
    padding-bottom: 17px;
  }
}
.feedback-form__input::placeholder {
  color: rgba(28, 28, 28, 0.4);
}
.feedback-form__input[name="name"]:invalid,
.feedback-form__input[name="email"]:invalid {
  background-image: url("../img/incorrect.svg");
  background-size: 12px 12px;
  background-position: center right 16px;
  background-repeat: no-repeat;
  outline: 2px solid #f97676;
}
.feedback-form__input[name="name"]:valid,
.feedback-form__input[name="email"]:valid {
  background-image: url("../img/correct.svg");
  background-size: 12px 9px;
  background-position: center right 18px;
  background-repeat: no-repeat;
  outline: 2px solid #1dc04b;
}
.feedback-form__input[name="name"]:focus,
.feedback-form__input[name="email"]:focus {
  border: none;
  outline: 2px solid #0dd1a2;
}
.feedback-form__input[name="question"]:focus {
  border: none;
  outline: 2px solid #0dd1a2;
}
.feedback-form__submit {
  margin-top: 15px;
  padding-top: 7px;
  padding-bottom: 7px;
}
@media (min-width: 1366px) {
  .feedback-form__fieldset {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    gap: 10px;
    margin: 0;
    padding: 0;
    border: none;
  }
  .feedback-form__fieldset label[aria-label="Имя"] {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }
  .feedback-form__fieldset label[aria-label="Email"] {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
  }
  .feedback-form__fieldset label[aria-label="Вопрос"] {
    grid-column: 1 / 3;
    grid-row: 2 / 3;
  }
  .feedback-form__input {
    margin-bottom: 0;
  }
}
.intro {
  padding-top: 68px;
  padding-bottom: 20px;
}
@media (min-width: 1366px) {
  .intro {
    padding-bottom: 30px;
    padding-left: 0;
  }
}
.intro__content-container {
  flex: 1 0 auto;
}
@media (min-width: 1366px) {
  .intro__content-container {
    padding-left: 56px;
  }
}
.intro__title {
  margin: 0;
  margin-bottom: 19px;
  padding-top: 13.7vh;
  font-size: 36px;
  font-weight: 400;
  line-height: 41px;
  text-align: center;
}
@media (min-width: 768px) and (max-width: 1365px) {
  .intro__title {
    margin-top: 85px;
    margin-bottom: 40px;
    font-size: 60px;
  }
}
@media (min-width: 1366px) {
  .intro__title {
    margin-bottom: 24px;
    padding-top: 158px;
    font-size: 96px;
    line-height: 110px;
    text-align: left;
  }
}
.intro__text {
  margin: 0;
  margin-bottom: 51px;
  padding-left: 31px;
  padding-right: 31px;
  line-height: 21px;
  text-align: center;
}
@media (max-width: 767px) {
  .intro__text {
    max-width: 320px;
    margin: 0 auto;
    margin-bottom: 51px;
    box-sizing: border-box;
  }
}
@media (min-width: 1366px) {
  .intro__text {
    margin-bottom: 71px;
    padding-left: 0;
    font-size: 17px;
    line-height: 26px;
    text-align: left;
  }
}
.intro__booking {
  margin: 0 auto;
  margin-bottom: 12px;
  text-decoration: none;
  background-image: url("../img/icons/order-icon.svg");
  background-size: 25px 25px;
  background-position: calc(50% - 57px) 50%;
  background-repeat: no-repeat;
}
@media (min-width: 1366px) {
  .intro__booking {
    display: inline-block;
    margin-bottom: 0;
    margin-right: 22px;
    background-size: 30px 30px;
    background-position: 37px 50%;
  }
}
.intro__video {
  margin: 0 auto;
  background-image: url("../img/icons/video-icon.svg");
  background-size: 25px 25px;
  background-position: calc(50% - 43px) 50%;
  background-repeat: no-repeat;
}
@media (min-width: 1366px) {
  .intro__video {
    min-width: 200px;
    padding-left: 76px;
    display: inline-block;
    background-size: 30px 30px;
    background-position: 35px 50%;
  }
}
.main-nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  padding-top: 5px;
  padding-bottom: 50px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.24);
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding-top: 24px;
    padding-bottom: 20px;
  }
}
@media (min-width: 1366px) {
  .main-nav__list {
    padding-top: 0;
    padding-left: 5px;
    padding-right: 0;
    padding-bottom: 0;
    border-bottom: none;
  }
}
.main-nav__item {
  position: relative;
  margin-bottom: 34px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__item--primary {
    display: none;
  }
}
.main-nav__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .main-nav__item {
    margin-bottom: 0;
    margin-right: 20px;
    padding: 5px;
  }
  .main-nav__item:last-child {
    margin-right: 0;
  }
}
@media (min-width: 1366px) {
  .main-nav__item {
    margin-right: 29px;
  }
  .main-nav__item:not(:last-child)::after {
    position: absolute;
    content: "";
    width: 4px;
    height: 4px;
    top: calc(50% - 2px);
    right: -15px;
    background-color: #b0acac;
    border-radius: 50%;
  }
}
.main-nav__link {
  padding-top: 8px;
  padding-bottom: 8px;
  font-weight: 500;
  line-height: 17px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .main-nav__item--active .main-nav__link {
    color: #0dd1a2;
  }
}
.main-nav__item:hover .main-nav__link {
  color: #0aa081;
}
.main-nav__item:active .main-nav__link {
  color: #0dd1a2;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
    border-bottom: none;
  }
  .main-nav--opened .main-nav__list {
    display: flex;
    width: 100%;
    padding-top: 50px;
    padding-left: 4px;
    background-color: #1c1c1c;
    border-bottom: 1px solid rgba(255, 255, 255, 0.24);
  }
}
.main-nav--nojs .main-nav__list {
  position: static;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.24);
}
@media (min-width: 1366px) {
  .main-nav--nojs .main-nav__list {
    border-bottom: none;
  }
}
.main-nav--nojs .main-nav__item {
  margin-bottom: 20px;
}
.order-box {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 320px;
  height: 568px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-style: normal;
  font-size: 15px;
  font-weight: 300;
  line-height: 19px;
  text-align: center;
  color: rgba(28, 28, 28, 0.64);
  background-color: #f3f3f3;
  box-sizing: border-box;
}
@media (min-width: 1366px) {
  .order-box {
    width: 560px;
    height: 338px;
    padding-left: 80px;
    padding-right: 80px;
  }
}
.order-card {
  padding-top: 136px;
}
.order-card__title {
  margin: 0;
  margin-bottom: 14px;
}
.order-card__text {
  margin: 0;
  margin-bottom: 21px;
}
@media (min-width: 1366px) {
  .order-card {
    height: 380px;
    padding-top: 60px;
  }
  .order-card__text {
    margin: 0;
    margin-bottom: 21px;
  }
}
.order-confirm {
  padding-top: 216px;
}
@media (min-width: 1366px) {
  .order-confirm {
    padding-top: 140px;
    padding-left: 84px;
    padding-right: 84px;
  }
}
.order-confirm__title {
  position: relative;
  margin: 0;
  margin-bottom: 10px;
}
.order-confirm__title::before {
  position: absolute;
  content: "";
  top: -80px;
  left: 50%;
  transform: translateX(-50%);
  width: 52px;
  height: 52px;
  background-color: transparent;
  border: 4px solid rgba(28, 28, 28, 0.1);
  border-radius: 50%;
}
.order-confirm__title::after {
  position: absolute;
  content: "";
  top: -85px;
  left: 50%;
  transform: translateX(-50%);
  width: 70px;
  height: 60px;
  background-image: url("../img/icons/check.svg");
  background-size: 55px 45px;
  background-position: 15px 9px;
  background-repeat: no-repeat;
}
.order-confirm__text {
  margin: 0;
  margin-bottom: 33px;
}
@media (min-width: 1366px) {
  .order-confirm__text br {
    display: none;
  }
}
.order-confirm__button {
  padding: 7px 34px;
  color: rgba(28, 28, 28, 0.4);
}
.order-confirm__button:hover,
.order-confirm__button:active {
  color: #ffffff;
}
.order-form .feedback-form__field {
  position: relative;
}
.order-form .order-form__input {
  width: 100%;
  padding: 15px;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-size: 13px;
  line-height: 17px;
  color: rgba(28, 28, 28, 0.95);
  background-color: #ffffff;
  border: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  box-sizing: border-box;
}
.order-form .order-form__input::placeholder {
  color: rgba(28, 28, 28, 0.4);
}
.order-form .order-form__input:invalid {
  outline: 2px solid #f97676;
  background-image: url("../img/incorrect.svg");
  background-size: 12px 12px;
  background-position: center right 16px;
  background-repeat: no-repeat;
}
.order-form .order-form__input:valid {
  outline: 2px solid #1dc04b;
  background-image: url("../img/correct.svg");
  background-size: 12px 9px;
  background-position: center right 18px;
  background-repeat: no-repeat;
}
.order-form .order-form__input:focus {
  border: none;
  outline: 2px solid #0dd1a2;
}
.order-form-wrapper-tel {
  position: relative;
  margin-top: 9px;
}
.order-form__pattern-tel {
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  padding: 15px;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-size: 13px;
  color: rgba(28, 28, 28, 0.4);
  line-height: 17px;
  text-align: left;
  letter-spacing: 0.05em;
  background-color: #ffffff;
  border: none;
  box-sizing: border-box;
  z-index: -1;
}
.order-form__input[name="order-tel"]:focus {
  background-color: transparent;
}
.order-form__input[name="order-tel"]:focus::placeholder {
  color: transparent;
}
.order-form__submit {
  margin: 0 auto;
  margin-top: 30px;
  padding: 7px 14px;
  color: rgba(28, 28, 28, 0.4);
}
.order-form__submit:hover,
.order-form__submit:active {
  color: #ffffff;
}
.order-title {
  font-family: "Ubuntu", "Arial", sans-serif;
  font-style: normal;
  font-size: 21px;
  font-weight: 700;
  line-height: 24px;
  text-align: center;
  color: #1c1c1c;
}
.order-toggle {
  position: absolute;
  content: "";
  top: 35px;
  right: 19px;
  width: 25px;
  height: 25px;
}
.order-toggle::before,
.order-toggle::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: rgba(28, 28, 28, 0.4);
}
.order-toggle::before {
  transform: rotate(45deg);
}
.order-toggle::after {
  transform: rotate(-45deg);
}
@media (min-width: 1366px) {
  .order-toggle {
    width: 17px;
    height: 17px;
    top: 23px;
    right: 16px;
  }
}
