.work__content-container {
  flex: 1 0 auto;
  padding-top: 60px;
  counter-reset: step;

  @media (min-width: @desktop-width) {
    padding-top: 75px;
  }
}

.work__title {
  margin: 0;
  margin-bottom: 56px;

  @media (min-width: @desktop-width) {
    margin-bottom: 44px;
  }
}

.work__slider {
  @media (min-width: @tablet-width) and (max-width: @tablet-width-only) {
    max-width: 100%;
  }
}
